@font-face {
  font-family: "fontawesome2";
  src: url('../../fonts/CRO55DM.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "fontawesome2", sans-serif;
}

.exam-container {
  margin-top: 200px;
  padding: 20px;
  max-width: 100%;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.exam-table {
  width: 100%;
  border-collapse: collapse;
}

.exam-table td {
  padding: 10px;
  text-align: left;
  color: rgba(11, 84, 219, 1);
  border: 1px solid #ddd;
}

.exam-table th {
  background-color: #176add;
  color: white;
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

.exam-table tbody tr:hover {
  background-color: #f1f1f1;
}
. add_intern {
  display: flex;
  justify-content: space-around;
}


.add_intern button {
background-color: #3057d1;
    border-radius: 20px;
    border-button: 1px solid white;
    border: none;
    cursor: pointer;
    padding: 5px;
}

.add_intern button:hover {
  background-color: rgba(11, 84, 219, 1);
}

.add_intern img {
  max-width: 100%;
  height: 100%;
}
.add_intern img:hover {
 color: white;
}
